export default {
	data() {
		return {
			list: [],
			pageNum: 1,
			pageSize: 20,
			loading: true,
			more: false
		};
	},
	onShareAppMessage() {
		let token = uni.getStorageSync('TOKEN');
		let user = uni.getStorageSync('userInfo');
		if (token) {
			return {
				title: '易建联共享服务平台',
				path: '/pages/index/index?server=' + user.server_people_id
			};
		}
	},
	onShow() {
		this.list = [];
		this.pageNum = 1;
		this.loading = true;
		this.getList();
	},
	onReachBottom() {
		this.getList();
	},
	methods: {
		toDetail(item) {
			if (item.status === 1) {
				uni.navigateTo({
					url: 'detail/detail?item=' + JSON.stringify(item)
				});
			}
		},
		getList() {
			this.$Fetch({
				url: 'advise/getData',
				data: {
					pageNum: this.pageNum,
					pageSize: this.pageSize
				},
				debug: true,
				success: res => {
					let len = res.length;
					if (len > 0) {
						this.list = this.list.concat(res);
						if (len === this.pageSize) {
							this.pageNum++;
							this.loading = true;
							this.more = true;
						} else {
							this.more = false;
						}
					} else {
						this.more = false;
					}
				}
			});
		}
	}
};
